import { Injectable } from "@angular/core";
import { Headers, Http, RequestOptions, Response } from "@angular/http";
import { ScriptLoaderService } from '../../_services/script-loader.service';
import { Staff } from "../_models/index";

@Injectable()
export class StaffService {
    constructor(private http: Http, private baseUrl: ScriptLoaderService) {
    }

    getAll() {
        return this.http.get(this.baseUrl._baseUrl + 'staff/getall', this.jwt()).map((response: Response) => response.json());
    }

    create(staff) {
        return this.http.post(this.baseUrl._baseUrl + 'staff/create', staff, this.jwt()).map((response: Response) => response.json());
    }

    getById(id: number) {
        return this.http.get(this.baseUrl._baseUrl + 'staff/edit/' + id, this.jwt()).map((response: Response) => response.json());
    }

    update(staff , id) {
        console.log(id);
        return this.http.post(this.baseUrl._baseUrl + 'staff/update/'+ id, staff, this.jwt()).map((response: Response) => response.json());
    }

    del_staff(id: number) {
        return this.http.get(this.baseUrl._baseUrl + 'staff/delete/' + id, this.jwt()).map((response: Response) => response.json());
    }

    
    // private helper methods

    private jwt() {
        // create authorization header with jwt token
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        if (currentUser && currentUser.token) {
            let headers = new Headers({ 'Authorization': 'Bearer ' + currentUser.token });
            return new RequestOptions({ headers: headers });
        }
    }
}
